.galeria {
    padding: 5rem 0;
    h3 {
        text-align: center;
    }
}

.galeria-imagenes {
    @include resetear-lista;
    @include grid(2,2rem);
    @include telefono{
        @include grid(3,2rem);
    }
    @include tablet{
        @include grid(4,2rem);
    }
    @include desktop{
        @include grid(6,2rem);
    }
}

.overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;

    display: grid;
    place-items: center; // para centrar vertical y horizontalmente

    background-color: rgb(0 0 0 / 0.1); // para que se oscurezca el fondo cuando este la imagen desplegada

    picture {
        display: block;
        max-width: 80rem;
    }
}

.btn-cerrar {
    width: 4rem;
    height: 4rem;
    border-radius: 50%;
    background-color: $blanco;
    color: $negro;
    display: grid;
    place-items: center;
    font-weight: 700;

    &:hover { // el & hace referencia en css al selector padre dentro del cual se encuentra la regla anidada, en este caso el hover
        cursor: pointer;
    }
}