.boletos {
    padding: 5rem 0;
    h3 {
        text-align: center;
        color: $blanco;
        margin-bottom: 2rem
    }

    .contenedor-boletos {
        @include tablet {
            @include grid(2,5rem);
        }
    }
}

.pase {

    .nombre-pase {
        text-align: center;
        font-size: 2.8rem;
        color: $blanco;
    }

    &-header, &-footer {
        padding: .5rem;
        p {
            text-align: center;
            color: $blanco;
            margin: 0;
        }
    }

    // .pase-header { podria ser así pero con & nos ahorramos código ya que es igual que la clase padre
    &-header {
        background-color: $rosa;
        p {
            text-transform: uppercase;
            font-size: 1.6rem;
        }
    }
    &-body {
        background-color: $blanco;
        ul {
            @include resetear-lista;
            li, :nth-child(odd) {
                background-color: $blanco;
                padding: 1rem;
                border-bottom: 1px solid #e1e1e1;
            }
        }
    }
    &-footer {
        background-color: $morado;
        p {
            font-size: 2.4rem;
            font-weight: 700;
        }
    }
}