
html {
    font-size: 62.5%; // nos asegura en todo el proyecto que 1 rem sea igual a 10 px
    box-sizing: border-box; // el snippet de Paul Irish
}

*,
*:before,
*:after {
    box-sizing: inherit; // para que el padding no nos suponga un problema.
}

body {
    font-family: $fuente_principal;
    font-size: 1.6rem;

    &.fijar-body {
        overflow: hidden; // lo que impide dar scroll cuando esta el overlay
    }
    &.body-scroll {
        padding-top: 86px; // para que no haya un salto en toda la pagina cuando aparece el header al hacer scroll hacia abajo. (el tamaño del header es de 86px, por eso le damos un padding hacia arriba, para compensarlo)
    }
}

p {
    font-size: 2rem;
    color: negro;
    line-height: 1.5;
}

.contenedor {
    width: 95%;
    max-width: 120rem;
    margin: 0 auto;
}

a {
    text-decoration: none;
}

h1, h2, h3 {
    margin: 0 0 5rem 0;
}

h1 {
    font-size: 4rem;
}

h2 {
    font-size: 3.6rem;
}

h3 {
    font-size: 3rem;
}

img {
    max-width: 100%;
    display: block;
    width: 100%;
    height: auto;
}