.header {
    background-color: $verde;
    @include tablet{
        &.fijo {
            left: 0;
            top: 0;
            position: fixed;
            width: 100%;
            box-shadow: .5rem .2rem .5rem #333;
        }
    }
    h1 {
        color: $blanco;
        text-align: center;
        @include tablet {
            margin: 0;
        }
    }
    .contenido-header {
        padding: 2rem;
        @include tablet {
            display:flex;
            justify-content: space-between;
            align-items: center;
        }
    }
}

.navegacion-principal {
    @include tablet {
        display: flex;
        gap: 2rem;
    }
    a {
        display: block;
        text-align: center;
        color: $blanco;
        font-size: 2.4rem;
        margin-bottom: 1rem;
        @include tablet {
            margin: 0;
        }
        &:hover { // el '&' es para que en la hoja de estilos css no se cree un espacio entre la 'a' y el ':hover'
            color: $amarillo;
        }
    }
}

