.lineup {
    background-color: $rosa;
    padding: 5rem 0;

    h3, p {
        text-align: center;
        color: $blanco;
    }
    .dia {
        text-transform: uppercase;
        margin: 5rem 0;
    }
}

.escenarios-contenedor {
    @include tablet {
        @include grid(2, 5rem);
    }

    p {
        color: $negro;
    }
}

.escenario {
    padding-top: 2rem;
    
    p {
        &:after {
            content: '';
            display: block;
            width: 20rem;
            height: 10rem;
            background-size: 15rem;
            background-repeat: no-repeat;
            background-position: center;
            margin: 2rem auto 0 auto;
        }
    }
}

.edm .nombre-escenario:after {
    background-image: url(../img/icono_edm.png);
}

.rock .nombre-escenario:after {
    background-image: url(../img/icono_rock.png);
}

.calendario {
    @include resetear-lista;
    li {
        padding: 2.5rem;
        font-weight: 700;
        span {
            font-weight: 400;
        }
    }
}

.bg-amarillo {
    background-color: $amarillo;
    li:nth-child(odd) { // odd selecciona los li impares
        background-color: darken($amarillo, 15%); // la propiedad darken es de css y te permite jugar con el porcentaje de oscuridad de un color.
    }
}

.bg-verde {
    background-color: $verde;
    li:nth-child(odd) { // odd selecciona los li impares
        background-color: darken($verde, 10%);
    }
}

