
.video {
    position: relative;
    max-height: 70rem;
    min-height: 50rem;
    overflow: hidden;
    .overlay {
        position: absolute;
        background: rgb(192,0,255);
        background: linear-gradient(270deg, rgba(192,0,255,0.5) 0%, rgba(255,0,78,0.3) 100%);
        height: 100%;
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
    video {
        width: auto;
        display: block;
        @include desktop {
            width: 100%;
        }
    }
}

.contenido-video {
    z-index: 1; // para superponer el texto por encima del video.
    h2 {
        color: $blanco;
        font-size: 7rem;
        margin: 0;
    }
    p {
        color: $blanco;
        background-color: $rosa;
        display: inline-block;
        padding: 1rem 2rem;
        font-weight: 700;
        font-size: 2.4rem;
        margin: 0;
    }
}